import React from 'react'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'
import styled from 'styled-components'

import Layout from '../components/layout'

const Container = styled.div`
  margin-top: 5em`
;

const ImageWrapper = styled.div`
  margin-bottom: 3.5em;
`;

const Content = styled.div`
  max-width: 880px;
  margin: 0 auto;
`;

const Tagline = styled.h2`
  font-family: "Proxima Nova Semi Bold";
  letter-spacing: -1.5px;
  font-size: 42px;
`;

const Timeline = styled.p`
  font-family: "Proxima Nova Light";
  font-size: 20px;
  line-height: 1.5;
  // color: #767676;
  margin-bottom: 0;
  // font-weight: 200;
`;

const Detail = styled.div`
  margin-bottom: 2em;
`;

const Dash = styled.div`
  margin-top: 2em;
  width: 60px;
  border-bottom: solid 5px #484848;
`;

const Section = styled.div`
  margin-top: 2.5em;
  max-width: 40rem;
`;

const Heading = styled.h3`
  font-family: "Proxima Nova Semi Bold";
  font-size: 26px;
  letter-spacing: -0.5px;
  margin-bottom: 1.5em;
`;

const List = styled.ul`
  margin-bottom: 0;
`;

const ListItem = styled.li`
  margin: 0.8em 0;
`

class Ohana extends React.Component {
  constructor() {
    super();
  }

  render() {
    return (
      <Layout>
        <Container>
          <ImageWrapper>
            <Img style={{maxHeight: "500px", minHeight:"350px"}} fluid={this.props.data.hero.childImageSharp.fluid} />
          </ImageWrapper>
          <Content>
            <Tagline>How do you bring household chore management into the 21st century</Tagline>
            <Detail>
              <Timeline>Project Title: Ohana</Timeline>
              <Timeline>Timeline: Spring 2018</Timeline>
              <Dash />
            </Detail>
            <Section>
              <p>Leaving the comfortable and tidy homes of your parents and moving into a house or apartment with others can be a stressful and disorganized ordeal. Splitting up responsibilities among your housemates oftentimes leads to conflict and overall awkwardness. Chores around the house can go unfinished, which leads to dirty and unhealthy living environments.</p>
              <p>The problem that my team chose to address is the lack of a modern means of responsibility sharing that is convenient and reliable. We felt that current whiteboard chore sheets depend too much on manual re-drawing while verbal organization is inherently unorganized.</p>
            </Section>
            <Section>
              <Heading>Stakeholders</Heading>
              <p>Our first step towards finding a solution to the problem outlined above was identifying the primary stakeholders for our application. After brainstorming with the team, we identified two stakeholders, young adults and early tech adopters. Our reasoning is that young adults are the most likely to have roommates. This includes college students and recent grads.</p>
            </Section>
            <Section>
              <table>
                <tr>
                  <th>Primary Stakeholders</th>
                  <th>Needs/Expectations</th>
                </tr>
                <tr>
                  <td>Young Adults</td>
                  <td>
                    <List>
                      <ListItem>A way to ensure their house/apartment stays clean</ListItem>
                      <ListItem>Needs a easy and convenient way to gather information due to limited time</ListItem>
                    </List>
                  </td>
                </tr>
                <tr>
                  <td>Early Tech Adopters</td>
                  <td>
                    <List>
                      <ListItem>A way to integrate the latest gadget into their everyday lives</ListItem>
                    </List>
                  </td>
                </tr>
              </table>
            </Section>        
            <Section>
              <Heading>Proposed Solution</Heading>
              <p>We intended to create an Alexa skill that would enable roommates and housemates to efficiently manage their household responsibilities. The primary means of adjusting settings for the skill will be through a web app which we will develop in combination with the skill. Our users will initially use a web application to add users and chores.</p>
              <p>We want to present the skill as a convenience over traditional ways of managing chores. We want our users to figure out their chores without pulling out a device or going to a computer. Because of Alexaâ€™s omnipresence (in the room, a few feet away from them), users are able to figure out their chores seamlessly. There are important distinctions to the two parts of our deliverable.</p>
            </Section>  
          </Content>
        </Container>
      </Layout>
    )
  }
}

export default Ohana;

export const query = graphql`
  {
    hero:file(relativePath: {eq:"images/nimbus-hero.png"}) {
      childImageSharp {
        fluid (quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`